.goalsOrHobitsInput{
    background: #FFFFFF 0% 0% no-repeat padding-box;
    border-radius: 20px;
    text-align: center;
    width: 60px;
    height: 25px;
    font-size: 11px;
    font-family: Assistant ;
}

.goalsOrHobitsInputOkStatus{
    border: 1px solid #4B9791;
    color: #9b8f8f;
}

.goalsOrHobitsInputWarningStatus{
    border: 2px solid #da3d21;
    color: #a54d4d;
}

.goalsOrHobitsBtnsContainer{
